* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}
body{
    width: 100vw !important;
    overflow-x: hidden !important;
}
::-webkit-scrollbar{
    width: 10px;
}

::-webkit-scrollbar-thumb {
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb:hover {
    background: #e0e0e0;
}

.setIconInput {
    /* position: absolute; */
    margin-right: -25px;
    margin-top: 5px;
    z-index: 2;
}

.editHover,
.deleteHover,
.viewTaskHover,
.markAsConpleted {
    transition: all 500ms;
}

.markAsConpleted:hover {
    background: #198754;
    color: white !important;
}

.viewTaskHover:hover {
    background: #6c757d;
    color: white !important;
}

.editHover:hover {
    background: #ffc107;
    color: white !important;
}

.deleteHover:hover {
    background: #dc3545;
    color: white !important;
}

.backbtn {
    display: grid;
    place-items: center;
    background: #52545514;
    border: none;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    font-size: 30px !important;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.backbtn:hover {
    background: #96989961;
}

.w-55{
    width: 55%;
}

.w{
    width: 45%;
}

.d-xs-none{
    display: none;
}

.manageOverflow{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
